//
function showRes(res) {
	console.log(res)
	if(res.status === 200) {
		if(res.data.code !== 2000) {
			uni.showToast({ title: ',', icon: 'none' })
		}
	}else if(res.status === 404) {
		uni.showToast({ title: ',', icon: 'none' })
	}else {
		uni.showToast({ title: ',', icon: 'none' })
	}
}

//catch
function showErr(err) {
	console.log(err)
	uni.showToast({ title: ',', icon: 'none' })
}

module.exports = {
	showRes,
	showErr
}