//:,
import { pinyin_dict_notone } from '@/js_sdk/pinyins/pinyin_dict_notone'
	
function isChinese(str) {
	//
	const reg1 = /[\u4E00-\u9FCC\u3400-\u4DB5\uFA0E\uFA0F\uFA11\uFA13\uFA14\uFA1F\uFA21\uFA23\uFA24\uFA27-\uFA29]|[\ud840-\ud868][\udc00-\udfff]|\ud869[\udc00-\uded6\udf00-\udfff]|[\ud86a-\ud86c][\udc00-\udfff]|\ud86d[\udc00-\udf34\udf40-\udfff]|\ud86e[\udc00-\udc1d]/
	
	//
	const reg2 = /[a-zA-Z]/
	
	if(str) {
		if(reg1.test(str)) {
			for(let key in pinyin_dict_notone) {
				const value = pinyin_dict_notone[key];
				if(value.indexOf(str) !== -1) {
					//
					return key
				}
			}
			//
			return false
		}else if(reg2.test(str)) {
			//
			return str
		}else {
			//
			return false
		}
	}else {
		return false
	}
}

//
function debounce(fn, wait=30) {
	let timer = null
	console.log('')
	return function() {
		if(timer !== null) {
			console.log('')
			clearTimeout(timer)
		}
		//
		timer = setTimeout(() => {
			console.log('')
			fn.apply(this, arguments)
		}, wait)
	}
}

//firstWorld
function formatList(data, firstWorld='friendRemarkName') {
	const newArr = []
	//
	const specialArr = []
	for(let i=0; i<26; i++) {
		const key = String.fromCharCode(65 + i) //A-Zkey
		
		newArr[i] = {
			letters: key,
			list: []
		}
		
		data.map((v) => {
			//
			const getFirstWorld = v[firstWorld];
			
			// friendRemarkName
			if(getFirstWorld) {
				//
				const fir = getFirstWorld.charAt(0)
				//
				const getFirstWorldType = isChinese(fir);
				if(getFirstWorldType) {
					// ,
					const UpperFirstWorld = getFirstWorldType.charAt(0).toUpperCase();
					if(UpperFirstWorld === key) {
						newArr[i].list.push(v)
					}
					
				}else {
					//
					//
					
					if(i===0) {
						specialArr.push(v)
					}
				}
			}else {
				//
				
				if(i===0) {
					specialArr.push(v)
				}
			}
		})
		
	}
	
	
	
	//
	const specialObj = {
		letters: "#",
		list: specialArr
	}
	//newArr
	newArr.push(specialObj)
	console.log(':', newArr)
	return newArr
}


//friendCard
function setNewestFriendCard(state, data) {
	let tempState = Object.assign({}, state)
	let newObj = {}
	data.forEach(item => {
		newObj[item.friendAccount] = item
	})
	return Object.assign({}, newObj, tempState.friendCard)
}









//   target
function deepClone(target) {
    // 
    let result;
    // 
    if (typeof target === 'object') {
    // 
        if (Array.isArray(target)) {
            result = []; // result
            for (let i in target) {
                // 
                result.push(deepClone(target[i]))
            }
         // nullnull
        } else if(target===null) {
            result = null;
         // RegExp    
        } else if(target.constructor===RegExp){
            result = target;
        }else {
         // for in
            result = {};
            for (let i in target) {
                result[i] = deepClone(target[i]);
            }
        }
     // 
    } else {
        result = target;
    }
     // 
    return result;
}

//
function chooseImg(count=9) {
	return new Promise((resolve, reject) => {
		uni.chooseImage({
			count,
			success: res => {
				console.log(':', res)
				resolve(res.tempFilePaths)
			},
			fail: err => {
				reject(err)
			}
		})
	})
}
//
function uploadFile(obj, filePath, formData=null) {
	return new Promise((resolve, reject) => {
		uni.uploadFile({
			url: obj.url,
			filePath: filePath,
			formData,
			name: obj.name,
			success: res => {
				resolve(res)
			},
			fail: err => {
				reject(err)
			}
		})
	})
}




